  color: white;
  font-family: "Pacifico", cursive;
  text-shadow: 0.0625rem 0.156rem 0.156rem rgb(88, 85, 85);
}

button {
  font-size: 1rem;
  font-weight: 600;
  color: white;
  background-color: rgb(30, 46, 77);
  border: none;
}

button:hover {
  background-color: rgba(30, 46, 77, 0.589);
  cursor: pointer;
}

@keyframes fade-slide-in {
  from {
    transform: translateY(-5rem);
  }
  to {
    transform: translateY(0);
  }
}
.flex-col {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.flex-col__gap20 {
  gap: 1.25rem;
}
.flex-col__gap50 {
  gap: 5.625rem;
}
.flex-col__gap70 {
  gap: 4.375rem;
}

.white-patch {
  width: 100%;
  max-width: 31.25rem;
  border-radius: 1.875rem;
  background-color: white;
  padding: 1.875rem;
}
.white-patch .search-field {
  border: 0.0625rem solid rgba(146, 144, 144, 0.603);
  outline: none;
  padding: 0.875rem;
  height: 3.125rem;
  border-radius: 1.875rem;
  margin-right: 0.3125rem;
}
.white-patch .search-button {
  font-size: 0.875rem;
  width: 4.375rem;
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 6.25rem;
}
.white-patch .create-icon-label {
  text-align: center;
  font-size: 1rem;
  color: rgb(30, 46, 77);
  font-weight: 500;
}
.white-patch .create-icon {
  width: 6.25rem;
  height: 6.25rem;
  border: 0.156rem dashed rgb(30, 46, 77);
  border-radius: 6.25rem;
  cursor: pointer;
}
.white-patch .create-icon:hover {
  border: 0.156rem dashed rgba(30, 46, 77, 0.589);
}
.white-patch .create-icon__cross {
  font-size: 2.125rem;
  font-weight: 500;
  color: rgb(30, 46, 77);
}
.white-patch .view-button {
  width: 7.5rem;
  height: 3.125rem;
  border-radius: 1.625rem;
}

